import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { CoreModule } from './core/core.module';
import { SharedModule } from './shared/shared.module';
import { LocationStrategy, PathLocationStrategy } from '@angular/common';
import { SkeletonComponent } from './layout/skeleton/skeleton.component';
import { FooterComponent } from './layout/footer/footer.component';
import { NavigationComponent } from './layout/navigation/navigation.component';
import { HomeComponent } from './components/home/home.component';
import { NosotrosComponent } from './components/nosotros/nosotros.component';
import { CapacitacionesComponent } from './components/capacitaciones/capacitaciones.component';
import { BoletinComponent } from './components/boletin/boletin.component';
import { FilosofiaComponent } from './components/filosofia/filosofia.component';
import { OrganigramaComponent } from './components/organigrama/organigrama.component';
import { DirectorioComponent } from './components/directorio/directorio.component';
import { ManualesComponent } from './components/manuales/manuales.component';
import { FormatosComponent } from './components/formatos/formatos.component';
import { RhComponent } from './components/rh/rh.component';
import { MedicaComponent } from './components/medica/medica.component';
import { MedidasComponent } from './components/medidas/medidas.component';
import { PacientesComponent } from './components/pacientes/pacientes.component';
import { CalendarioComponent } from './components/calendario/calendario.component';


@NgModule({
  declarations: [
    AppComponent,
    SkeletonComponent,
    FooterComponent,
    NavigationComponent,
    HomeComponent,
    NosotrosComponent,
    CapacitacionesComponent,
    BoletinComponent,
    FilosofiaComponent,
    OrganigramaComponent,
    DirectorioComponent,
    ManualesComponent,
    FormatosComponent,
    RhComponent,
    MedicaComponent,
    MedidasComponent,
    PacientesComponent,
    CalendarioComponent,
  ],
  imports: [
    BrowserModule,
    // Core
    CoreModule,
    SharedModule,
    AppRoutingModule
  ],
  providers: [
    {
      provide: LocationStrategy,
      useClass: PathLocationStrategy
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
